import { useState, useEffect } from 'react';
import { Typography, Input, List } from 'antd';
import JobItems from '../components/JobItem';
import * as Services from '../services';

export default function Search() {
  useEffect(() => {
    document.title = "Fakejobs - Cek Lowongan Palsu | Cari"
  }, []);

  return (
    <div className="container-fluid">
      <Typography.Title>Cek Lowongan Pekerjaan Bodong</Typography.Title>
      <Typography.Text>Kamu dapan mengecek berdasarkan email, nama perusahan, judul lowongan, dan lain-lain</Typography.Text>
      <br/><br/>
      <SearchForm />
    </div>
  )
}

function SearchForm() {
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState([]);

  const onSearch = (value) => {
    setLoading(true);
    Services.getSearchFakeJobs(value).then(res => {
      setResult(res);
      setLoading(false);
    });
  }

  return (
    <div>
      <Input.Search 
        size="large"
        placeholder="Masukkan Kata Kunci (Email/Nama Perusahaan/Lowongan)" 
        onSearch={onSearch}
        loading={loading}
        enterButton />
        <br /><br />
      <SearchResult result={result} loading={loading} />
    </div>
  )
}

function SearchResult({ result, loading }) {
  return (
    <List
      loading={loading}
      itemLayout="horizontal"
      dataSource={result}
      renderItem={item => (
        <List.Item>
          <JobItems item={item}/>
        </List.Item>
      )}
    />
  )
}